<template> 
    <div>
        <head>
             <meta name="viewport" content="width=device-width, initial-scale=1">
            <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.4.0/css/bootstrap.min.css">
        </head>
        <main class="container">
            <form class="form-group">
                <label for=sellOp>Operation</label>
                 <select required autofocus class="selectOperation" id="sellOp" v-model="selected">
                    
                    <option disabled value="">Select a Operation</option>
                    <option @click="emptyDoc">FIND</option>
                    <option>CREATE</option>
                    <option>LIST</option>
                    <option>DELETE</option>
                    <option>UPDATE</option>
                 
                 </select>

                    <input v-if="selected == 'UPDATE'" type="text" placeholder="What a planet?" v-model="planet"/>
                    <input type="text" required placeholder="Name" v-model="name"/>
                    <input type="text" placeholder="Climate" v-model="climate"/>
                    <input type="text" placeholder="Land" v-model="land"/>
            </form>
                <button v-if="selected == 'FIND'" type="submit" class="btn btn-default" @click="find()">Search</button>
                <button v-if="selected == 'CREATE'" type="submit" class="btn btn-default" @click="create()">Search</button>
                <button v-if="selected == 'UPDATE'" type="submit" class="btn btn-default" @click="update()">Search</button>
                <button v-if="selected == 'DELETE'" type="submit" class="btn btn-default" @click="deletePlanet()">Search</button>
                <button v-if="selected == 'LIST'" type="submit" class="btn btn-default" @click="list()">Search</button>
        <br>
        <table v-if="doc" class="table table-hover">
            <thead>
            <tr>    
            <th>ID</th>
            <th>Name</th>
            <th>Climate</th>
            <th>Land</th>
            <th>Version</th>
            </tr>
            </thead>
            <tbody>
            <tr v-if="selected !== 'LIST'">
            <td v-for="(value,chave) in doc" :key="chave">{{value}}</td>
            </tr>
            <!--
                <tr v-else v-for="(value,chave) in doc" :key="chave"> 
            <td v-for="docs in value" :key="docs._id">{{docs}}</td>
            </tr>
            -->
            </tbody>    
        </table>
        </main>
       
    </div>
</template>
<script>
import axios from 'axios'

export default {
    data(){
        return {
            id:"",
            name:"",
            climate:"",
            land:"",
            doc:"",
            planet:"",
            docIshover:false,
            report:"",
            selected:"",
        }

    },
    computed:{
        emptyDoc(){
            return this.doc ==""
        }
    },
    methods:{

       find(){
           if(this.id){
                axios.get('http://localhost:9080/find/'+this.id).then((res)=>{
                    this.doc == res.data
                })
           }
           else{
                axios.get('http://localhost:9080/find?name='+this.name+
                "&climate="+this.climate+
                "&land="+this.land).then( res => {
                    this.doc = res.data
                })
           }
             
            
        },
        
       list(){
            axios.post('http://localhost:9080/list',{'name':" "}).then((res)=>this.doc = res.data)
        },
        create(){
              axios.post('http://localhost:9080/create',{'name':this.name,'climate':this.climate,'land':this.land})
           .then((res)=>this.doc = res.data)
             
        },
        deletePlanet(){
            axios.delete('http://localhost:9080/delete',{data:{'name': this.name}})
            .then((res)=>this.doc = res.data)
        },
        update(){
            axios.patch('http://localhost:9080/update/'+this.planet+'',{'name':this.name,'climate':this.climate,'land':this.land})
        },

    }

}


</script>
<style>
    .selectOperation{
        width: 100px;
        height: 30px;
        margin:2px;
        background-color: ghostwhite;
    }
    tr{
        width: 200px;
    }
</style>